html {
	height:100%;
	min-height: 100%
}

body {
	height:100%;
	min-height: 100%
}

header {
	height: 100px;
	background-color: #363c3c;
	-webkit-box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.2);
	        box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.2);

}

header img {
  margin-left: 30px;
  margin-top: 24px;
  margin-bottom: 23.6px;
}

#searchbar{
	width: 320px;
	height: 60px;
	border-radius: 10px;
	background-color: #ffffff;
	border: solid 1px #363c3c;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-right: 40px;
  float: right;
}



header #searchbar input{
	height: 29px;
	opacity: 0.6;
	font-family: 'Source Serif Pro', serif;
	font-size: 24px;
	color: #363c3c;
  margin-top: 17px;
  margin-left: 21px;
  margin-bottom: 14px;
  margin-right: 29px;
  border-style: none;
	outline: none;
	width:280px

}

#sidecolumn {
	height: 100%;
	width:100%;
	background-color: #d6d8d8;
	-webkit-box-shadow: 2px 0 6px 0 rgba(0, 0, 0, 0.2);
	        box-shadow: 2px 0 6px 0 rgba(0, 0, 0, 0.2);
	border: solid 1px #adadad;
	float:left
}

#sidecolumn button {
  width: 100%;
  height: 60px;
  border-radius: 10px;
  background-color: #ffffff;
  border: solid 1px #363c3c;
  font-size: 24px;
  font-weight: bold;
  margin:0 auto;
  font-family: 'Source Serif Pro', serif;

}

#sidecolumn #color-randomizer {
  margin-top: 80px;
  margin-bottom: 37px;
  text-align: center;
  padding:0 30px
}


#sidecolumn a {
  width: 49px;
	height: 35px;
  font-family: 'Source Serif Pro', serif;
	font-size: 28px;
	text-align: left;
	color: #363c3c;
  margin-top: 177px;
	list-style: none;

}

#sidecolumn li {
  margin-bottom: 18px;
	list-style: none;
}

#sidecolumn li:last-child {
  margin-bottom: 0px
}

#pagination ul {
	font-size: 24px;
	font-family: SourceSerifPro;
	text-align: center;
	list-style: none;
	color: #363c3c;
	padding-top: 80px;
	margin: 0 auto
}

#pagination ul li {
	display: inline;
	margin:0 14px
}

#pagination ul li a{
	color: #363c3c
}

#sidecolumn-trigger {
	position: absolute;
	z-index:10;
	margin-left:-15px;
	padding:15px;
}

#sidecolumn-trigger img {
	cursor: pointer;
}



.preview-swatch {
  width: 100%;
  border-radius: 10px;
  border:1px solid #363c3c;
  overflow: hidden;
  margin-top: 80px;

}

.preview-swatch .swatch-color {
  height: 552px;
	background-color: #cffff1;
}

.preview-swatch .swatch-label {
  height: 100px;
	background-color: #fff;
}
.preview-swatch .swatch-label h1 {
  width: 161.9px;
  height: 60px;
  font-family: 'Source Serif Pro', serif;
  font-size: 48px;
  text-align: center;
  color: #363c3c;
  margin-left: 40px;
  margin-bottom:36px;
  margin-top: 36px;
}


.small-preview-swatch1 {
  width: 100%;
  height:175px;
  border-radius: 9px;
  border:1px solid #363c3c;
  overflow: hidden;
  margin-top: 80px;

}
.small-preview-swatch1 .small-swatch-color1 {
  height: 109px;
}
.small-preview-swatch1 .small-swatch-label1 {
  height: 100px;
	background-color: #fff;
}
.small-preview-swatch1 .small-swatch-label1 h1 {
  width: 71px;
	height: 30px;
	font-family: SourceSerifPro;
	font-size: 24px;
	text-align: center;
	color: #363c3c;
  margin-left: 22.5px;
  margin-bottom: 16px;
  margin-top: 19px;
}

#clear-button {
  margin-top: 40px;
  margin-bottom: 38px;
  text-align: center;
  /*margin: 0 auto;*/
}

#clear-button button {
	width: 260px;
	height: 60px;
	font-family: 'Source Serif Pro', serif;
	font-size: 24px;
	font-weight: bold;
	text-align: center;
	color: #363c3c;
  margin-left: 50.5px;
  margin-right: 49.5px;
  margin-top: 16px;
  margin-bottom: 14px;
  border-radius: 10px;
	background-color: #ffffff;
	border: solid 1px #363c3c;
}


/* Custom 5ths (2.4) Column */
.col-xs-5ths,
.col-sm-5ths,
.col-md-5ths,
.col-lg-5ths {
    position: relative;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
}

.col-xs-5ths {
    width: 20%;
    float: left;
}

@media (min-width: 768px) {
    .col-sm-5ths {
        width: 20%;
        float: left;
    }

			#sidecolumn-trigger {
				display: none;
			}

			#sidecolumn {
				position: inherit;;
				display: block!important;
				visibility: visible!important;
			}
}

@media (min-width: 992px) {
    .col-md-5ths {
        width: 20%;
        float: left;
    }
}

@media (min-width: 1200px) {
    .col-lg-5ths {
        width: 20%;
        float: left;
    }
}

@media (max-width: 768px) {
    #color-randomizer button {
			font-size:18px
		}

		#sidecolumn-trigger {
			display: block
		}

		header #searchbar {
			width:100%;
			overflow: hidden;
		}

		header #searchbar input {
			width:90%
		}

		#sidecolumn {
			position: absolute;
    	z-index: 9;
    	max-width: 320px;
			display: none
		}
}

@media (max-width: 492px) {
    #color-randomizer button {
			font-size:14px
		}
}


.no-left-padder {
  padding-left:0
}
.no-right-padder {
  padding-right:0
}



